import {VueEditor} from 'vue2-editor'
import {URL_Q_AND_A} from '../../../conf/router'

export default {
    computed: {
        rules: () => ({
            title: [
                {required: true, message: '', trigger: 'change'}
            ]
        })
    },

    data() {
        return {
            edit: false,
            id: '',
            form: {
                title: '',
                content: '',
            }
        }
    },

    components: {
        VueEditor
    },

    mounted() {
        this.refresh()

        const id = this.getQuery('id')
        if (id) {
            this.edit = true
            this.id = id
            this.$store.dispatch('qaDetail', {
                data: {id},
                success: data => {
                    if (data) {
                        this.form.title = data.title
                        this.form.content = data.content
                    }
                }
            })
        }
    },

    methods: {
        onKeywordChange() {
            this.refresh(this.list.page)
        },

        refresh(page = 1) {
            this.loading = true
            this.$store.dispatch('authUserList', {
                data: {
                    page,
                    keyword: this.keyword
                },
                success: data => {
                    this.list = data
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        },

        onSubmit() {
            this.$refs['form'].validate(valid => {
                if (!valid) {
                    return
                }
                this.$store.dispatch(this.edit ? 'qaEdit' : 'qaAdd', {
                    data: {
                        id: this.edit ? this.id : '',
                        ...this.form
                    },
                    success: data => {
                        data && this.redirect(URL_Q_AND_A)
                    },
                    error: msg => {
                        this.alert(msg)
                    }
                })
            })
        },

        handleImageAdded(file, Editor, cursorLocation, resetUploader) {
            const formData = new FormData()
            formData.append('key', 'qa')
            formData.append('image', file)
            this.$store.dispatch('uploadImage', {
                data: formData,
                success: data => {
                    if (data && data.url) {
                        Editor.insertEmbed(cursorLocation, 'image', data.url)
                        resetUploader()
                    }
                },
                error: msg => {
                    this.alert(msg)
                }
            })
        }
    }
}