import {URL_Q_AND_A_ADD, URL_Q_AND_A_EDIT} from '../../../conf/router'

export default {
    computed: {
        urlEdit: () => URL_Q_AND_A_EDIT
    },

    data() {
        return {
            loading: true,
            dialogVisible: false,
            dialogRow: {},
            list: {
                items: []
            }
        }
    },

    components: {},

    mounted() {
        this.refresh()
    },

    methods: {
        onAddClick() {
            this.redirect(URL_Q_AND_A_ADD)
        },

        onKeywordChange() {
            this.refresh(this.list.page)
        },

        refresh() {
            this.loading = true
            this.$store.dispatch('qaList', {
                success: data => {
                    this.list = data
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        },

        view(row) {
            this.dialogVisible = true
            this.dialogRow = row
        },

        viewClose() {
            this.dialogVisible = false
        },

        request(action, data) {
            this.$store.dispatch(action, {
                data: data,
                success: data => {
                    if (data && data.success) {
                        this.refresh()
                    }
                },
                error: msg => {
                    this.alert(msg)
                }
            })
        },

        remove(row) {
            this.confirm(`[${row.title}]`, () => {
                this.request('qaDelete', {id: row.id})
            })
        },

        move(row, up) {
            this.confirm(`${up ? '' : ''}`, () => {
                this.request('qaMove', {id: row.id, up: up ? '1' : '0'})
            })
        }
    }
}